.img {
  height: 1.5rem;
  width: 1.5rem;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.live {
  border-bottom: 2px solid black;
  font-weight: bolder;
  color: red;
}

.logo:hover {
  font-weight: bolder;
  border-bottom: 2px solid black;

}

.main {
  position: sticky;
  top: 5rem;
  z-index: 9999;
  background-color: white;
  width: 93rem;

}

.btn {
  display: flex;
  flex-direction: row;
  border-radius: 0.5rem;
  height: 2rem;
  width: 7rem;
  align-items: center;
  justify-content: space-between;
  font-size: 1rem;
  background-color: white;
  cursor: pointer;
}

.container {
  display: flex;
  align-items: center;
  gap: 1rem;
  position: sticky;
  top: 5rem;
  z-index: 9999;
  background-color: white;
  width: 85rem;

}

.dialog {
  height: 85vh;
  width: 50vw;
  background-color: white;
  border-radius: 2rem;
  position: absolute;
  margin-left: 20rem;
  z-index: 1;
  box-shadow: 0px 0px 10px 2px;

}

.input {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.property {
  display: flex;
  justify-content: center;

}